


.index-position{
  z-index: 1;
  padding: 0;
}
.app-ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  background-color: #333;
}

.app-ul > li {
  float: left;
}

.app-ul > li a{
  display: block;
  color: white;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
}

.app-ul > li a:hover {
  background-color: #111;
}
.center-title{
  margin-bottom: 30px;
  text-align: center;
  font-weight: 600;
  padding-top:60px;
}

.sticky{
  position: fixed;
  top: 0;
  width: 100%;
  margin-bottom: 200px;
}
.content{
  margin-top: 65px;
}
.bottom-right-div{
  bottom: 0;
  right: 0;
  position: fixed;
  height: 50px;
  width: 50px;
  text-align: center;
  line-height: 55px;
  margin-bottom: 30px;
  margin-right: 30px;
  border-radius: 50%; 
  z-index: 10;
  cursor: pointer;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.5), 0 6px 20px 0 rgba(0, 0, 0, 0.5);
}

@media only screen and (max-width: 600px) {
  .app-ul > li > a > span{
    font-size: 12px;
  } 
}